import VisualComponent from "jx/core/comps/VisualComponent";
/**
  * @type {Object}
  * @property {string} render
  * @property {number} quality
  */
let defaultValues = {
    render: null,
    quality: 1,
    x: 0,
    y: 0,
    width: undefined,
    height: undefined,
    ratioWidth: undefined,
    ratioHeight: undefined,
    alpha: 1,
    anchor: null,
    anchorX: 0,
    anchorY: 0,
    enabled: true,
    visible: true,
    rotation: 0
};
/**
 * @ignore
 * All visual components implements Animation class.
 */
export default class Animation extends VisualComponent {
    /**
     * @param {Object} dataObject - this is object param.
     */
    constructor(dataObject) {
        super(Object.assign({
            framerate: 24
        }, dataObject));
        this.implements("Animation");
        //_____________________________
        // EVENTS
        this.onplaying = new signals.Signal();
        this.onfinished = new signals.Signal();
    }
    /**
    * @override
    */
    _firstInit() {
        super._firstInit();
    }
    get duration() {
        return this.view.duration;
    }
    get currentTime() {
        return this.view.currentTime;
    }
    set currentTime(value) {
        this.view.currentTime = value;
    }
    get framerate() {
        return this.view.frameRate;
    }
    set framerate(value) {
        this.view.frameRate = value;
    }
    play(playParams) {
        this.view.play(playParams);
    }
    stop() {
        this.view.stop();
    }
    cancel() {
        this.view.cancel();
    }
    goto(gotoParams) {
        this.view.goto(gotoParams);
    }
}
